import React from 'react';
import { BseIconSelect } from './BaseTextSelect';
import { ITextSelectProps } from './interface';
import { useToolbarItemConfig } from '../common/hooks/useToolbarItemConfig'

export type { ITextSelectProps as ITBTextSelectProps };
export const TBTextSelect = React.memo((props: ITextSelectProps) => {
  const { itemSize } = useToolbarItemConfig();

  return (itemSize === 'large') ? null : (<BseIconSelect size={itemSize} {...props} />)
});

TBTextSelect.displayName = 'textSelect';